  width: 80%;

  /* font-family: myFont; */
}

.Profile {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}

.Nav {
  width: 100%;
  display: inline-block;
  margin: 0;
  background-color: #646464;
  background-image: linear-gradient(#4aa8ff, #98cdff);
  padding-left: 30px;
  height: 60px;
  font-family: 'Lilita One', cursive;
  font-size: 30px;
  padding-bottom: 0;
  margin-bottom: 30px;
}

.Nav .navbar_link, .Nav #signUpDiv, .Nav #userLogin, .Nav p, .Nav button {
  display: inline;
  padding-right: 40px;
}

.Nav .navbar_link {
  color: #FFF9C7;
  height: 60px;
  margin: 0;
}

.Nav .navbar_link:hover {
  color: #202020;
}

#menuScreen, #canvasGameScreen {
  position: relative;
}
#menuBackground, #menuScreenOne, #menuScreenTwo {
  position: absolute;
}
.levelSprite, .levelSpriteText, #menuScreenTwoText, .optionsText {
  position: absolute;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  color: rgb(255, 255, 255);
  font-size: 30px;
	-webkit-text-stroke-width: 0.4px;
  -webkit-text-stroke-color: black;
  
  text-shadow: 0 0 20px black;
  font-family: 'Lilita One', cursive;
  line-height: 1.1;
}
#menuScreenTwo td {
  text-align: center;
}
#menuScreenTwoText {
  font-size: 70px;
  top: 70px;
	-webkit-text-stroke-width: 0.8px;
}
#menuScreenTwoImg {
  height: 200px;
}
.backBtn {
  position: absolute;
  right: 20px;
  top: 20px;
}
.backBtn:hover {
  cursor: pointer; 
}
.levelSprite:hover {
  transform: scale(1.1);
  transition: 0.5s;
  cursor: pointer; 
}
.timesLevel, #optionsLevel {
  width: 100px;
  height: 50px;
  font-size: 20px;
  border-radius: 50px;
  border: solid 2px black;
  text-align: center;
}
.timesLevel:hover {
  border: solid 2px black;
  background-color: #acacac;
}

#gameScreen {
  border: solid 1px black;
  top: 0px;
  left: 0px;
  z-index: -1;
}
audio {
  display: none;
}

form {
  width: 20em;
}

button {
  margin-right: 10px;
}
#resetBtn, #lvlResetBtn, #nextBtn, #fullScrBtn {
  position: absolute;
  bottom: 20px;
  right: 40px;
  padding-left: 40px;
  padding-right: 40px;
}
#lvlResetBtn {
  bottom: 100px;
}
#fullScrBtn {
  right: 900px;
}
#fullScrBtnCls {
  z-index: 100;
}
#fullScrBtnCls {
  display: none;
  position: absolute;
  top: 3px;
  right: 0px;
  opacity: 0.75;
}

@font-face {
  font-family: "harryPotterFont";
  src: local("harryPotterFont"), url("./harryPotterFont.TTF") format("truetype");
}
@font-face {
  font-family: "bladeRunnerFont";
  src: local("bladeRunnerFont"), url("./bladeRunnerFont.TTF") format("truetype");
}
@font-face {
  font-family: "goodTimesFont";
