}


/* sound effect input switch */
.--light {
  background: #c7c7cc;
}
.--light:checked {
  background: #34c759;
}
.ios-switch {
  appearance: none;
  outline: none;
  border: 0;
  border-radius: 1em;
  position: relative;
  width: 3.4em;
  height: 2em;
  overflow: hidden;
}
.ios-switch:after {
  content: "";
  position: absolute;
  width: 1.6em;
  height: 1.6em;
  top: 0.2em;
  left: 0.2em;
  background: white;
  border-radius: 50%;
  transition: all 0.1s;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);
}
.ios-switch:checked:after {
  transform: translateX(1.4em);
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.2);
}
#speedImgID {
  width: 170px;
}
.speenBtn {
