.store {
  overflow: auto;
  display: flex;
}
.store .character {
  float: left;
  border: solid 1px black;
  border-radius: 25px;
  padding: 10px;
  margin-right: 10px;
  text-align: center;
  width: 180px;
  height: 250px;
  position: relative;
}
.store .character h5, .store .character img, .store .character button {
  position: absolute;
}
.store .character h5 {
  top: 20px;
  width: 85%;
}
.store .character img {
  top: 70px;
  width: 100px;
  height: 100px;
  left: 40px;
}
.store .character button {
  bottom: 10px;
  left: 10px;
  width: 90%;
  border-radius: 20px;
}

#inputAns {
  position: absolute;
  z-index: 10;  
  width: 76px;
  height: 34px;
  font-size: 20px;
  text-align: center;
  font-family: goodTimesFont;
}

.loader {
  border: 12px solid #f3f3f3;
  border-radius: 50%;
  border-top: 12px solid #3498db;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.Signup, .Login {
  margin-left: 20px;
}